@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');

.App {
  text-align: center;
  font-family: 'Poppins', sans-serif;
  background: #dde0e3;
}
button{
  padding: 15px 40px;
  font-weight: 600;
  background: #dde0e3;
  font-size: 18px;
  border-radius: 8px;
  color: #800080;
  border-width: 0px;
  box-shadow: -5px -5px 5px #ffffff80, 5px 5px 5px #00000020;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #80008095;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #800080;
}
