/* welcome section */
.welcome{
    height: 90vh;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.welcome .welcomeRow{
    -webkit-align-items: center;
            align-items: center;
    margin: 0%;
    padding: 0%;
}
.welcome .welcomeCol{
    -webkit-align-items: center;
            align-items: center;
    text-align: left;
}
.welcome .welcomeCol h1{
    margin: 50px;
    margin-bottom: 30px;
    margin-top: 10px;
}
.welcome .welcomeCol h6{
    margin-left: 50px;
    color: #888;
    font-size: 20px;
    max-width: 500px;
}
.welcome .welcomeCol .keywords{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: left;
            justify-content: left;
    margin-left: 50px;
}
.welcome .welcomeCol .keywords button{
    background: #80008010;
    padding: 7px 25px;
    border-radius: 15px;
    margin: 10px;
    margin-left: 0px;
}
.welcome .welcomeCol .searchForm{
    height: 60px;
    display: -webkit-flex;
    display: flex   ;
    box-shadow: inset -5px -5px 10px #ffffff80, inset 5px 5px 5px #00000020;
    margin-left: 50px;
    border-radius: 50px;
    max-width: 600px;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 10px;
}
.welcome .welcomeCol .searchForm input{
    height: 100%;
    width: 75%;
    border: none;
    background: transparent;
    outline: none;
    margin-left: 20px;
    font-size: 20px;
    color: #555;
}
.welcome .welcomeCol .searchForm input::-webkit-input-placeholder{
    font-size: 20px;
    color: #88888870;
}
.welcome .welcomeCol .searchForm input:-ms-input-placeholder{
    font-size: 20px;
    color: #88888870;
}
.welcome .welcomeCol .searchForm input::placeholder{
    font-size: 20px;
    color: #88888870;
}
.welcome .welcomeCol .searchForm button{
    margin: auto;
    border-radius: 50px;
    padding: 10px 40px;
    font-size: 20px;
    font-weight: 400;
    margin-right: 5px;
    color: #dde0e3;
    background: #800080;
    box-shadow: none;
}

/* loading images section */

.loadingImages{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    background: #dde0e3;
    min-height: 90vh;
    -webkit-align-items: center;
            align-items: center;
}
.images{
    position: relative;
    width: 200px;
    height: 180px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}
.imageSlider{
    max-height: 70vh;
    margin-top: 30px;
}
.imageSlider img{
    max-height: 100%;
    width: auto;
}
/* what i can do section */
.whatICanDo{
    border-radius: 10px;
    box-shadow: -10px -10px 20px #ffffff80, 10px 10px 20px #00000020;
    height: 320px;
    width: 300px;
}
.whatICanDo h3{
    color: #000;
    font-size: 22px;
    font-weight: 700;
}

/* our services */
.services{
    background-image: url('https://res.cloudinary.com/dmixz7eur/image/upload/v1619370959/Adidas_ypwebs.png');
    height: 400px;
    width: 250px;
    background-position: center;
    background-size: cover;
    margin: 10px;
    text-align: left;
}
.services h3{
    margin: 10px;
}
.services h4{
    margin: 10px;
    margin-bottom: 0px;
}
.navbar{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin: 0;
    padding: 0%;
    width: 100%;
    height: 80px;
    box-shadow: 0px 5px 10px #00000020;
}
.navbar .navLogo{
    display: -webkit-flex;
    display: flex;
    max-width: 140px;
    margin-left: 50px;

}
.navbar .navLinks{
    margin: auto;
    padding: 0;
}
.navbar .navLinks ul{
    display: -webkit-flex;
    display: flex;
    padding: 0px;
    margin: 0px;
}
.navbar .navLinks ul li{
    margin: 5px;
    padding: 10px;
    font-weight: 600;
    box-shadow:  -3px -3px 3px #ffffff80,  3px 3px 3px #00000020;
    border-radius: 5px;
    color: #888;
    cursor: pointer;
}
.navbar .navLinks ul .active{
    color: #800080;
    box-shadow: inset -3px -3px 3px #ffffff80, inset 3px 3px 3px #00000020;
}
.navbar .hire{
    margin-right: 50px;
}


